<template>
  <v-card>
    <v-form ref="addCompany">
      <v-card>
        <v-card-text>
          <v-row dense>
            <!-- <v-col class="d-flex"
                 cols="12"
                 sm="6">
            <v-text-field :label="$t('label_company_name') + '*'"
                          
                          v-model="params.org_level_name"
                          :error-messages="$errors('params','org_level_name',maxLength(20).concat(requiredRule))"
                          @input="$v.params.org_level_name.$touch()"
                          @blur="$v.params.org_level_name.$touch()"></v-text-field>
          </v-col>
          <v-col class="d-flex"
                 cols="12"
                 sm="6">
            <v-text-field :rules="$text_rules('短编码')"
                          :label="$t('label_Short_code') + '*'"
                          
                          :error-messages="$errors('params','org_level_shortcode',maxLength(10).concat(requiredRule))"
                          @input="$v.params.org_level_shortcode.$touch()"
                          @blur="$v.params.org_level_shortcode.$touch()"
                          v-model="params.org_level_shortcode"></v-text-field>
          </v-col>
          <v-col class="d-flex"
                 cols="12"
                 sm="12">
            <v-text-field :error-messages="$errors('params','sequence',between('1-99').concat(requiredRule))"
                          @input="$v.params.sequence.$touch()"
                          @blur="$v.params.sequence.$touch()"
                          v-model="params.sequence"
                          :label="$t('label_The_sorting') + '*'"></v-text-field>
          </v-col> -->
            <v-col cols="6">
              <mrc_ess_multiText :label="$t('label_company_name') + '*'"
                                 :isNational="judegNational('org_level_name')"
                                 v-model="params.org_level_name"
                                 :error-messages="$errors('params','org_level_name',maxLength(20).concat(requiredRule))"></mrc_ess_multiText>
            </v-col>
            <v-col cols="6">
              <mrc_ess_multiText :rules="$text_rules($t('label_Short_code'))"
                                 :label="$t('label_Short_code') + '*'"
                                 :isNational="judegNational('org_level_shortcode')"
                                 :error-messages="$errors('params','org_level_shortcode',maxLength(10).concat(requiredRule))"
                                 v-model="params.org_level_shortcode"></mrc_ess_multiText>
            </v-col>
            <v-col cols="12">
              <!-- <v-text-field :error-messages="$errors('params','sequence',between('1-99').concat(requiredRule))"
                          @input="$v.params.sequence.$touch()"
                          @blur="$v.params.sequence.$touch()"
                          v-model="params.sequence"
                          :label="$t('label_The_sorting') + '*'"></v-text-field> -->
              <mrc_ess_NumInput :error-messages="$errors('params','sequence',between('1-99').concat(requiredRule))"
                                v-model="params.sequence"
                                :label="$t('label_The_sorting') + '*'"></mrc_ess_NumInput>
            </v-col>
            <v-col cols="12">
              <!-- obj="{prompt_name:'org_level_relevance_client',client_uuid: $store.state.userData.currentCompany.values.client_uuid}" -->
              <mrc_ess_prompt :rules="false"
                              :disabled='false'
                              :errMsg="$errors('params','org_level_code_parent',requiredRule)"
                              :label="$t('label_The_higher_the_unit') + '*'"
                              :clearable="true"
                              obj='new_parent_org_level'
                              v-model="params.org_level_code_parent">
              </mrc_ess_prompt>
            </v-col>
          </v-row>
        </v-card-text>
        <v-card-actions>

        </v-card-actions>
      </v-card>
    </v-form>
    <mrc_ess_submitBtn @cancel='cancel'
                       :loading='loading'
                       @success='success'></mrc_ess_submitBtn>
  </v-card>

</template>

<script>
import { get_company_add } from '@/service/api/company_set'
import { required, maxLength, between } from 'vuelidate/lib/validators'
export default {
  components: {},
  props: {},
  data () {
    return {
      loading: false,
      params: {
        org_level_name: '',
        org_level_shortcode: '',
        org_level_code_parent: '',
        sequence: ''
      }
    };
  },
  validations: {
    params: {
      org_level_name: {
        required,
        maxLength: maxLength(20)
      },
      org_level_shortcode: {
        required,
        maxLength: maxLength(10)
      },
      sequence: {
        required,
        between: between(1, 99)
      },
      org_level_code_parent: {
        required
      }
    }
  },
  created () { },
  async mounted () {
    await this.national('mrbase_sys_org_levels')
  },
  methods: {
    //   添加单位
    async addCompany () {
      this.$v.params.$touch()
      if (this.$v.params.$error) return
      this.loading = true
      const add = await get_company_add({ ...this.params, client_uuid: this.$store.state.currentCompanyClientUuid })
      if (add.code == 200) {
        this.$store.commit('newsTips', this.$t('message_The_operation_was_successful'))
        await this.$store.dispatch('set_topcompanylist')
        this.$router.go(-1)
      }
      this.loading = false
    },
    //   取消
    cancel () {
      this.$myConfirm({
        content: this.$t('message_Confirm_the_cancellation_of_the_edit')
      }).then(() => {
        this.$router.go(-1)
      }).catch(() => { })
    },
    // 提交
    async success () {
      await this.addCompany()
    }
  },
  computed: {},
  watch: {},
};
</script>

